/* ---------------------------------------------------
     Loading
----------------------------------------------------- */
.loading-page {
  display: flex;
  justify-content: center;
  flex-flow: column;
  height: 100%;  
  width: 100%;
	background-color: #ededed;
}

/* ---------------------------------------------------
     Alert
----------------------------------------------------- */
.alert-custom {
  position: fixed;
  right: 3px;
  top: 20px;
  margin-top:0;
  max-width: 98vw;
  min-width: 20vw;
  z-index: 1000;
  border: 1px solid #155724;
  visibility: visible;
}

.alert-enter {
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.alert-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: opacity 300ms, -webkit-transform 300ms;
  transition: opacity 300ms, transform 300ms;
  transition: opacity 300ms, transform 300ms, -webkit-transform 300ms;
}

.toggen-enter {
  opacity: 0.01;
  -webkit-transform: scale(0.9) translateY(50%);
          transform: scale(0.9) translateY(50%);
}
.toggen-enter-active {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
  transition: all 300ms ease-out;
}
.toggen-exit {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0%);
          transform: scale(1) translateY(0%);
}
.toggen-exit-active {
  opacity: 0.01;
  -webkit-transform: scale(0.9) translateY(50%);
          transform: scale(0.9) translateY(50%);
  transition: all 300ms ease-out;
}

@-webkit-keyframes animation-alert {
  from {
    opacity: 0;
    right: -300px;
  }
  to {
    opacity: 1;
    right: 3px;
  }
}
@keyframes animation-alert {
  from {
    opacity: 0;
    right: -300px;
  }
  to {
    opacity: 1;
    right: 3px;
  }
}
.run-animation-alert {
  -webkit-animation: animation-alert 1.2s ease;
          animation: animation-alert 1.2s ease;
}

@-webkit-keyframes animation-alert-hidden {
  from {
    opacity: 1;
    right: 3px;
  }
  to {
    opacity: 0;
    right: -99vw;
  }
}
@keyframes animation-alert-hidden {
  from {
    opacity: 1;
    right: 3px;
  }
  to {
    opacity: 0;
    right: -99vw;
  }
}
.run-animation-alert-hidden {
  -webkit-animation: animation-alert-hidden 2.5s ease;
          animation: animation-alert-hidden 2.5s ease;
}
